.recent-articles {
  overflow: hidden; }
  @media (min-width: 576px) {
    .recent-articles {
      padding: 10rem 0 10rem 10rem !important; } }
  .recent-articles__header {
    padding: 1rem 0; }
    @media (min-width: 576px) {
      .recent-articles__header .row .col-10 {
        padding-right: 30rem; } }
    .recent-articles__header .row .col-10 p {
      margin: 0 0 0 0;
      letter-spacing: 6px;
      text-transform: uppercase;
      font-size: 12px;
      color: #c50000; }
  .recent-articles__wrapper {
    padding: 1rem 0; }
    .recent-articles__wrapper__slider__row {
      padding: 0 1.3rem;
      box-shadow: 1px 1px 8px black; }
      .recent-articles__wrapper__slider__row__left-card {
        padding: 1rem 1rem 2rem 1.5rem;
        background-color: whitesmoke;
        color: black; }
        .recent-articles__wrapper__slider__row__left-card h4 {
          padding: 1rem 0 1rem 0;
          height: 7rem;
          overflow: scroll; }
        .recent-articles__wrapper__slider__row__left-card .left-card-date {
          margin: 0 0 4rem 0;
          font-size: 12px;
          color: #c50000;
          text-transform: uppercase;
          letter-spacing: 4px; }
          .recent-articles__wrapper__slider__row__left-card .left-card-date .month-span {
            letter-spacing: 6px !important; }
        .recent-articles__wrapper__slider__row__left-card a {
          color: black;
          transition: ease 0.25s;
          text-transform: uppercase;
          font-size: 12px; }
        .recent-articles__wrapper__slider__row__left-card a:hover {
          color: #c50000; }
      .recent-articles__wrapper__slider__row__right-card img {
        width: 100%;
        max-width: 100%; }

.loading-animation {
  width: 100%;
  height: 100%;
  padding: 6rem 0;
  margin: 0 auto;
  text-align: center; }
  .loading-animation__icon .fa-spinner {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    animation-name: loading-spinner;
    animation-duration: 1.3s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-out; }

@keyframes loading-spinner {
  from {
    transform: rotateZ(0deg); }
  to {
    transform: rotateZ(360deg); } }
